import infoBox from "../../components/boxstyle/infobox.vue"

export default {
	data() {
		return {
			carNum: "dshdjsd",
			carInfoList: [],
			carModeList: ['自翻卸车', '四轴车', '六轴车'],
		}
	},

	methods: {
		async getDriverInfo() {
			const opts = {
				url: '/sc/carMng/getDriverCarsByPage',
				method: 'post'
			};
			const res = await this.$http.httpTokenRequest(opts);
			this.carInfoList = res.data.result;

			console.log(res)
		}
	},
	onLoad() {
		this.getDriverInfo()
	},

	components: {
		infoBox
	}


}
